<template>
<div>
    <el-menu :router="true" :default-active="activeIndex" class="el-menu-demo" mode="horizontal"  background-color="rgb(70, 180, 147) ">
        <el-menu-item index="/home/usertop" >处理中心</el-menu-item>       
            <el-menu-item v-if="!isAdmin" @click="loginIn()">上班打卡</el-menu-item>
    </el-menu>
</div>
</template>

<style scoped>
.el-menu-item{
  color: aliceblue;
}

</style>

<script>
export default {
  data () {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      isAdmin:this.$store.state.isAdmin
    }
  },
  methods:{
    loginIn(){
      
      console.log("上班打卡")
      alert("打卡成功！")
    },
  }
}
</script>



